body{
    background-color: #1f1b1f;
    color:#1f1b1f;

    font-size: 16px;
    font-weight: normal;
    font-family: 'Roboto', sans-serif;

    /*font: normal 16px 'Roboto', sans-serif*/
}
.App{
    width: 100%;
    margin: auto;
    padding-top: 30px;
    display: flex;
}

.container h1{
    text-align:center;
}

.main_search{
    margin-bottom: 20px;
    display: flex;
}
.search_id{
    width: 200px;
}
.search_id label{
    margin-left: 30px;
    display: block;
    font-size: 14px;
    color: #86898f;
}
.side_panel{
    width: 300px;
}
.search_id input,
.search_id textarea{
    margin-left: 30px;
    width: 250px;
    box-sizing: border-box;
    border: none;
    background: transparent;
    border-bottom: 1px solid #ccc;
    color: #000;
    padding: 15px 0 10px;
    outline: none;
    font-family: inherit;
}

.btn {
    background:#111;
    padding: 15px 20px;
    color: #fff;
    border: none;
    font-family: inherit;
    cursor: pointer;
    border-radius: 10px;
    margin-right: 20px;
    height: 50px;
}

.btn:hover {
    opacity: 0.9;
}
.search_tracks{
    background: #1f1b1f;
    border-radius: 10px;
}
.search_tracks h2{
    color: rgb(255, 255, 255);
    text-align:center;
}
.track{
    font-family: sans-serif;
    width: auto;
    margin: 0 auto;
    display: flex;
    margin-bottom: 1px;
    margin-left: 15px;
    padding: 5px;
    color: rgb(255, 255, 255);
    border-radius: 10px;
}
.search_tracks > .track:last-child{
    margin-bottom: 15px;
}

.card{
    padding: 15px 20px;
    margin-bottom: 20px;
    border-radius: 20px;
}

.card h2{
    margin-bottom: 10px;
    color: rgb(255, 255, 255);
}
.bg-red{
    background: #1f1b1f;
}
a{
    color: rgb(255, 255, 255);
}
.link-list{
    list-style: none;
    padding: 0;
}
.link-list li{
    margin-bottom: 10px;
    padding-bottom: 5px;
    border-bottom: 1px solid #ccc;
    color: white;
}

.data label{
    display: block;
    font-size: 25px;
    color: #000000;
    text-align:center;
}

.data textarea{
    width: 100%;
    box-sizing: border-box;
    border: none;
    background: black;
    border-bottom: 1px solid #ccc;
    color: rgb(255, 255, 255);
    padding: 15px 10px 10px;
    outline: none;
    font-family: inherit;
    border-radius: 10px;
}

.main_action{
    display: inline;
    width: 100%;
}

.tabs{
    background: #1f1b1f;
    border-radius: 10px;
    padding: 15px 10px 10px;
    margin: 5px;
    color: white;
}

.edit_view{
    width: 20%;
    background-color: #1f1b1f;
    border-radius: 10px;
    margin: 5px;
    padding: 15px 10px 10px;
}

.visibility{
    font-family: sans-serif;
    width: auto;
    margin: 0 auto;
    display: flex;
    margin-bottom: 1px;
    margin-left: 15px;
    padding: 5px;
    background: #1f1b1f;
    color: rgb(255, 255, 255);
    border-radius: 10px;
}
.brightness{
    color: blanchedalmond;
}
.transparency{
    color: rgb(255, 255, 255);
}
.colorback{
    color: rgb(255, 255, 255);
}
.model{
    background: burlywood;
    height: 800px;
    padding: 5px;
    border-radius: 10px;
}
